








































import uniDrawer from '@/components/uni-drawer.vue';
export default {
	components: {
		uniDrawer,
	},
	data(){
		return {
			
			
			sidePull: {
				moveSele: false,//是否可以多选
				default: ['0|1','','1'],//注意只有多选的时候才可以'|'来分割默认选中那个 重置的话也是重置到默认的值

				showPropertyType:false,//是否显示弹框
				propertyTypeListValue: [],//选中的值
				propertyTypeList:[
					{
						name: "第一列",
						field: "brand_id",
						value: [
							{
								cid: "177",
								name: "水",
								active: false,
							},
							{
								cid: "181",
								name: "电",
								active: false,
							},
							{
								cid: "85",
								name: "木",
								active: false,
							},
							{
								cid: "3",
								name: "瓦",
								active: false,
							},
							{
								cid: "86",
								name: "油",
								active: false,
							},
						]
					},
					{
						name: "第二列",
						field: "cat_id",
						value: [
							{
								cid: "372",
								name: "水222",
								active: false,
							},
							{
								cid: "22",
								name: "电222",
								active: false,
							},
						]
					},
					{
						name: "第三列",
						field: "cat_id2",
						value: [
							{
								cid: "372",
								name: "水333",
								active: false,
							},
							{
								cid: "22",
								name: "电333",
								active: false,
							},
						]
					},
				],
			}
		}
	},
	methods: {
		btn(){
			this.showPropertyTypeFn();
		},
		propertyTypeSubmit(){
			var vm=this;
			vm.hidePropertyType();
			//propertyTypeListValue 获取当前选中的值
			vm.getSele();
			
			console.log('当前选中的值！');
			console.log( vm.sidePull.propertyTypeListValue );
		},
		hidePropertyType(){
			var vm=this;
			vm.sidePull.showPropertyType=false;
		},
		showPropertyTypeFn(){
			var vm=this;
			vm.sidePull.showPropertyType=true;
		},
		propertyTypeItem(index,index1){
			var vm = this;
			//修改当前选中的样式
			if(vm.sidePull.moveSele){
				//多选
				vm.sidePull.propertyTypeList[index].value.forEach((value,nub)=>{
					if(index1 == nub){
						value.active ? value.active = false : value.active = true;
					};
				});
			} else {
				//单选
				vm.sidePull.propertyTypeList[index].value.forEach((value,nub)=>{
					if(index1 == nub){
						value.active = true;
					} else {
						value.active = false;
					};
				});
			};
		},
		propertyTypeReset(){
			var vm=this;
			//重置默认值数据
			if(vm.sidePull.default.length > 0){
				//需要默认数据
				const def = vm.sidePull.default;
				vm.sidePull.propertyTypeList.forEach((item, nub)=>{
					if((def.length - 1) >= nub){
						const curr = def[nub].split('|');
						item.value.forEach((value,index)=>{
							value.active = curr.includes( index.toString() );
						});
						
					} else {
						console.log('default默认值有部分没选择！没选择部分已经置位已经全部置位false')
						item.value.forEach((value,index)=>{
							value.active = false;
						});
					};
				});
				
				//propertyTypeListValue 获取当前选中的值
				vm.getSele();
			}else {
				//不需要默认值。
				vm.sidePull.propertyTypeList.forEach(item=>{
					item.value.forEach((value,index)=>{
						value.active=false;
					});
				});
				vm.sidePull.propertyTypeListValue = [];
			};
		},
		getSele(){
			const vm = this;
			vm.sidePull.propertyTypeListValue=[];
			vm.sidePull.propertyTypeList.forEach(item=>{
				let _Arr = [];
				item.value.forEach((value,index)=>{
					if(value.active){
						_Arr.push(value.cid);
					};
				});
				if(_Arr.length > 0){
					let _arr = {
						name: item.field,
						value: _Arr.join('|')
					};
					vm.sidePull.propertyTypeListValue.push(_arr);
				};
			});
		},
	},
}
